import Footer from '@/components/Footer.vue';
import List from '@/components/List.vue';

@Component({
  components: {
    Header,
    Footer,
    List,
  },
})
export default class Tags extends Vue {
  private setTag(tag: string): void {
    this.$store.commit('setTag', tag);
  }
}
